.container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  
  .word {
    writing-mode: vertical-rl;
    text-orientation: upright;
    font-size: 4rem;
    width: 100vh;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .letter{
    writing-mode: horizontal-tb;
  }
  